import React, { Component } from 'react';
import img from '../../img/travisevans.jpg';

import restaurantApiPng from '../../projects/RandomRestaurant/thumbnail.png';

export default class Home extends Component {
    render() {
        return (
            <div className='container py-3'>
                <div className='container-fluid'>
                    <div className='row intro align-items-center'>
                        <div className='col-sm-5'>
                            <img src={img} className='img-fluid rounded-circle' alt='Travis Evans' />
                        </div>
                        <div className='col-sm-7'>
                            <div className='row'>
                                <div>
                                    <h3>Travis Evans</h3>
                                </div>
                            </div>
                            <div className='row divider-red'></div>
                            <div className='row'>
                                <h6 className='career-text my-1'>Web Developer</h6>
                            </div>
                            <div className='row'>
                                <p>Highly Motivated individual with software development skills willing to be put to good use. Great communication skills and teamwork with others as demonstrated in previous work experience.</p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='container-fluid'>
                    <div className='row justify-content-center'>
                        <h2>Example Projects</h2>
                    </div>
                    <div className='row justify-content-center'>
                        <div className='col-sm-4'>
                            <div className='row'>
                                <img href={restaurantApiPng} alt='Restaurant api project thumbnail' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
