import React from 'react';
import {Navbar, Nav} from 'react-bootstrap';
import {NavLink, Link} from 'react-router-dom';

const header = () => {
    return(
        <Navbar expand='lg' className='navbar-dark'>
            <Link className='logo-link' to='/'><h1>Travis <span>Evans</span></h1></Link>
            <Navbar.Toggle aria-controls='navbarNav' />
            <Navbar.Collapse id='navbarNav'>
                <Nav className='ml-auto'>
                    <NavLink exact to='/' className='nav-link'>Home</NavLink>
                    <NavLink to='/about' className='nav-link disabled'>About</NavLink>
                    <NavLink to='/resume' className='nav-link disabled'>Resume</NavLink>
                    <NavLink to='/portfolio' className='nav-link disabled'>Portfolio</NavLink>
                    <NavLink to='/contact' className='nav-link disabled'>Contact</NavLink>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default header;